/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
#root,
#__next {
  isolation: isolate;
}

body {
  background-color: #f2f6fe;
  line-height: 1.5;
  min-height: 100vh;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: 'SEGA LOGO FONT';
	font-style: normal;
	font-weight: normal;
	src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
	margin-top: 0;
	font-family:  'SEGA LOGO FONT';
	font-weight: 200;
	color:  #5950a8;
}

.bg-lightest-purple {
	background-color: #dcdcfa;
}

.title-leader {
	margin-top: 10px;
}

.card-image {
	margin-left: auto;
	margin-right: auto;
	max-width: 80%;
}
.cardlist-wrap {
	margin-top: 2em;
}
.search {
	border: 2px solid transparent;
}

.search:focus {
	border: 2px solid #dcdcfa;
	outline: none;
	position: relative;
}
